/*	Author: Angel Minguez Burillo																										*/
/*	Date: 23/8/2017																														*/
/****************************************************************************************************************************************/
/* Imports */
import { Injectable } from '@angular/core';								//Injectable decorator
import { Observable } from 'rxjs';										//Observable module 
import { Subject } from 'rxjs/Subject';  								//Subject module
/* Injectable metadata */
@Injectable()
/* Service */
export class ActivationService {
	private subject : Subject<boolean>;									//Subject object, it will emit booleans
	public activationStatus : boolean;									//Status flag: true if the user access the app through the confirmation e-mail link
	/* Constructor */
	constructor() {
		this.subject = new Subject<boolean>();							//Instanciason of the subject
		this.activationStatus = false;									//Initialization of the status flag
	}
	fromValidation(flag: boolean) {										//Method to force the Subject to emit a boolean value
		this.subject.next(flag);										//Subject will emit the parameter passed
		this.activationStatus = flag;									//Actualization of the status flag
	}
	isFromValidation(): Observable<boolean> {							//Method to obtain the Observable
		return this.subject.asObservable();								//The subject observable is returned
	}
}



