import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';				//Modal service
@Component({
	selector: 'app-register-success-modal',
	templateUrl: './register-success-modal.component.html',
	encapsulation: ViewEncapsulation.None,
	styleUrls: ['./register-success-modal.component.css']
})
export class RegisterSuccessModalComponent implements OnInit {
	constructor(public activeModal: NgbActiveModal) { }
	closeModal():void {
		this.activeModal.close();
	}
	ngOnInit() {}
}



